
.recruit-detail {
  background-color: #efeff4;
  height: 100%;
  padding: 10px;
}
.recruit-detail .qrimg-wrap {
  text-align: center;
  margin: 10px 0px;
}
.recruit-detail .tip-text {
  text-align: center;
  color: #fa8919;
  margin-top: 10px;
}
.recruit-detail .title {
  font-size: 16px;
}
.recruit-detail .link {
  color: #999;
  word-wrap: break-word;
  word-break: break-all;
  margin: 10px 0px;
}
.recruit-detail .copy-btn {
  background: #fa8919;
  border-radius: 5px;
  color: #fff;
  padding: 5px 10px;
  font-size: 18px;
  text-align: center;
}



