
.recruit-page {
  background-color: #efeff4;
  height: 100%;
}
.recruit-page .title {
  text-align: center;
  font-size: 18px;
  padding: 10px 0px;
  color: #fa8919;
}
.item-row {
  padding: 10px;
  background: #fff;
  border-bottom: 1px solid #ddd;
  display: flex;
  flex-direction: row;
}
.item-row .name-list {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.item-row .name-list .name-item {
  font-size: 14px;
  height: 24px;
  line-height: 24px;
}
.item-row .link {
  width: 100px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.item-row .link .link-btn {
  background: #fa8919;
  border-radius: 2px;
  color: #fff;
  padding: 5px 10px;
  font-size: 12px;
  text-align: center;
}



